import crypto from "crypto";
import { type OAuthConfig, type OAuthUserConfig } from "next-auth/providers";

/**
 * Last.fm OAuth Provider for Next Auth
 *
 * Last.fm OAuth flow:
 * 1. User redirected to Last.fm auth URL with API key and callback
 * 2. User authorizes, Last.fm redirects back with token param
 * 3. Exchange token for session key via auth.getSession API
 * 4. Store session key for authenticated API calls
 */

interface LastfmProfile {
	user: {
		id?: string; // Last.fm doesn't always return id - use name as unique identifier
		name: string;
		realname?: string;
		url?: string;
		image?: Array<{ size: string; "#text": string }>;
		registered?: { unixtime: string };
		playcount?: string;
	};
}

/**
 * Generate Last.fm API signature (MD5 hash)
 * Required for all Last.fm API calls with session key
 */
function generateApiSig(
	params: Record<string, string>,
	apiSecret: string
): string {
	const sorted = Object.keys(params)
		.sort()
		.map((k) => `${k}${params[k]}`)
		.join("");

	return crypto
		.createHash("md5")
		.update(sorted + apiSecret)
		.digest("hex");
}

/**
 * Exchange Last.fm auth token for session key
 */
async function getSessionKey(
	token: string,
	apiKey: string,
	apiSecret: string
): Promise<string> {
	const params: Record<string, string> = {
		api_key: apiKey,
		method: "auth.getSession",
		token: token,
	};

	const api_sig = generateApiSig(params, apiSecret);

	const queryParams = new URLSearchParams({
		...params,
		api_sig,
		format: "json",
	});

	const response = await fetch(
		`https://ws.audioscrobbler.com/2.0/?${queryParams}`,
		{
			method: "GET",
		}
	);

	if (!response.ok) {
		throw new Error(
			`Failed to get Last.fm session key: ${response.statusText}`
		);
	}

	const data = (await response.json()) as {
		session?: { key: string; user: string };
		error?: number;
		message?: string;
	};

	if (data.error) {
		throw new Error(
			`Last.fm API error (${data.error}): ${data.message}`
		);
	}

	if (!data.session?.key) {
		throw new Error("No session key returned from Last.fm");
	}

	return data.session.key;
}

/**
 * Get Last.fm user profile using session key
 */
async function getUserProfile(
	apiKey: string,
	sessionKey: string
): Promise<LastfmProfile> {
	const params: Record<string, string> = {
		api_key: apiKey,
		method: "user.getInfo",
		sk: sessionKey,
	};

	const api_sig = generateApiSig(params, process.env.LASTFM_API_SECRET || "");

	const queryParams = new URLSearchParams({
		...params,
		api_sig,
		format: "json",
	});

	const response = await fetch(
		`https://ws.audioscrobbler.com/2.0/?${queryParams}`,
		{
			method: "GET",
		}
	);

	if (!response.ok) {
		throw new Error(
			`Failed to get Last.fm user profile: ${response.statusText}`
		);
	}

	const data = (await response.json()) as {
		user?: LastfmProfile["user"];
		error?: number;
		message?: string;
	};

	if (data.error) {
		throw new Error(
			`Last.fm API error (${data.error}): ${data.message}`
		);
	}

	if (!data.user) {
		throw new Error("No user data returned from Last.fm");
	}

	return { user: data.user };
}

export const lastfmProvider: OAuthConfig<LastfmProfile> = {
	id: "lastfm",
	name: "Last.fm",
	type: "oauth",
	version: "2.0",

	// Set clientId/Secret to Last.fm credentials
	// NOTE: These are used for the auth link generation, but the actual OAuth callback
	// is handled by the custom route at /api/auth/callback/lastfm/route.ts
	// This avoids Auth.js trying to validate the implicit flow
	clientId: process.env.LASTFM_API_KEY || "",
	clientSecret: process.env.LASTFM_API_SECRET || "",

	// Last.fm authorization endpoint
	authorization: {
		url: "https://www.last.fm/api/auth",
		params: {
			api_key: process.env.LASTFM_API_KEY || "",
			cb: `${process.env.NEXTAUTH_URL || "http://127.0.0.1:3000"}/api/auth/callback/lastfm`,
		},
	},

	// Remove OAuth 2.0 standard parameters that Last.fm doesn't support
	authorizationParams() {
		return {
			scope: "",
			response_type: "",
			client_id: "",
			redirect_uri: "",
		};
	},

	// Custom token handler (won't be used - custom route handles callback)
	// But required by Auth.js for validation
	token: {
		url: "https://ws.audioscrobbler.com/2.0",
		async request() {
			// This won't be called because custom route handles the callback
			// Return a placeholder so Auth.js doesn't error
			return {
				tokens: {
					access_token: "",
					token_type: "Bearer",
				},
			};
		},
	},

	// Minimal userinfo for Auth.js validation (won't be called)
	userinfo: {
		url: "https://ws.audioscrobbler.com/2.0",
		async request({ tokens }) {
			// Won't be called - custom route returns profile directly
			return {
				user: {
					id: "",
					name: "placeholder",
				},
			};
		},
	},

	// Map Last.fm profile to NextAuth user object
	// Use username as ID since Last.fm doesn't return id field
	profile(profile: LastfmProfile) {
		return {
			id: profile.user.name, // Last.fm uses username as unique identifier
			name:
				profile.user.name ||
				profile.user.realname ||
				"Last.fm User",
			email: `${profile.user.name}@last.fm`,
			image:
				profile.user.image?.find((img) => img.size === "large")?.[
					"#text"
				] || null,
		};
	},

	// Last.fm branding
	style: {
		logo: "/lastfm-logo.png",
		bg: "#df0000",
		bgDark: "#df0000",
		text: "#fff",
		textDark: "#fff",
	},

	// Skip all checks - custom route handles validation
	checks: [],
} as OAuthConfig<LastfmProfile>;

export default lastfmProvider;
